<template xmlns:v-finger="http://www.w3.org/1999/xhtml">
  <transition name="funcList">
    <div v-show="uploadStatus" class="func-operation">
      <div class="background-chooser" @mousedown="moveBackground" @mousemove="moveBackground" @mouseup="moveBackground" @touchstart="mobileCancelMove" v-finger:pinch="mobileScaleBackground" v-finger:press-move="mobileMoveBackground" v-finger:double-tap="mobileScaleBackground2">
        <div class="exhibition"></div>
        <div v-if="!bg" class="picker pointer" @click="background" v-finger:tap="background"></div>
        <div class="clear pointer" @click="clearBackground" v-finger:tap="clearBackground" v-else>
          <div></div>
        </div>
        <input id="bg_img" type="file" @change="bgPicker"><br>
      </div>
      <!-- bg_params带参数 -->
      <div v-for="(item, index) in newsInfo" v-bind:key="index" class="newsInfo" :class="{'active': index === inputIndex && index !== 4, 'error': item.error && index !== 4}">
        <textarea v-if="index === 4" class="news-content" :class="{'active': index === inputIndex, 'error': item.error}" cols="30" rows="10" :placeholder="item.name" @click="focus(index)" @input="write(index)" @blur="deFocus"></textarea>
        <input type="text" :placeholder="item.name" :style="renderInfo(index)" @click="focus(index)" @input="write(index)" @blur="deFocus" v-else>
      </div>
      <div id="submit" class="pointer" @click="submit">提交</div>
    </div>
  </transition>
</template>

<script>
import $ from 'jquery'
import lrz from '../../node_modules/lrz/dist/lrz.all.bundle'
export default {
  name: 'news-upload',
  data () {
    return {
      uploadStatus: false,
      bg: false,
      newsInfo: [
        {name: '标题', id: 'title', error: false},
        {name: '日期', id: 'date', error: false},
        {name: '来源', id: 'source', error: false},
        {name: '超链接', id: 'url', error: false},
        {name: '请输入新闻内容', id: 'content', error: false}
      ],
      drag: false,
      currentX: 0,
      currentY: 0,
      inputIndex: -1,
      bgStyle: {
        scale: 125,
        posX: 50,
        posY: 50
      }
    }
  },
  methods: {
    background: function () {
      $('#bg_img').click()
    },
    moveBackground: function (event) {
      if (this.bg) {
        if (!event) {
          event = window.event
        }
        switch (event.type) {
          case 'mousedown':
            this.drag = true
            var e = event
            this.currentX = e.pageX
            this.currentY = e.pageY
            break
          case 'mousemove':
            if (this.drag) {
              var nowX = event.pageX
              var nowY = event.pageY
              var disX = 12 * (nowX - this.currentX)
              var disY = 12 * (nowY - this.currentY)
              var pos = $('.exhibition').css('background-position').split('%')
              var x = pos[0] - disX / $('.exhibition').width()
              var y = pos[1] - disY / $('.exhibition').height()
              if (x < 0) {
                x = 0
              } else if (x > 100) {
                x = 100
              }
              if (y < 0) {
                y = 0
              } else if (y > 100) {
                y = 100
              }
              var str = x + '% ' + y + '%'
              $('.exhibition').css('background-position', str)
              console.log($('.exhibition').css('background-position'))
            }
            break
          case 'mouseup':
            this.drag = false
            break
        }
      }
    },
    mobileCancelMove: function (e) {
      e.preventDefault()
    },
    mobileScaleBackground: function (e) {
      if (this.bg) {
        // parseInt($("#test").css("transform").split(/[\(\)]/)[1].split(',')[0])
        this.bgStyle.scale = e.zoom * this.bgStyle.scale
        if (this.bgStyle.scale > 250) {
          this.bgStyle.scale = 250
        } else if (this.bgStyle.scale < 125) {
          this.bgStyle.scale = 125
        }
        $('.exhibition').css('background-size', this.bgStyle.scale + '%')
      }
    },
    mobileScaleBackground2: function () {
      if (this.bg) {
        var temp = $('.exhibition').css('background-size')
        var original = parseInt(temp.substring(0, temp.length - 1))
        this.bgStyle.scale = original * 1.4
        if (this.bgStyle.scale > 250) {
          this.bgStyle.scale = 250
        }
        $('.exhibition').css('background-size', this.bgStyle.scale + '%')
      }
    },
    mobileMoveBackground: function (e) {
      if (this.bg) {
        var temp = this.bgStyle.posX - e.deltaX / $('.exhibition').width() * 100
        if (temp >= 0 && temp <= 100) {
          this.bgStyle.posX = temp
        }
        temp = this.bgStyle.posY - e.deltaY / $('.exhibition').height() * 100
        if (temp >= 0 && temp <= 100) {
          this.bgStyle.posY = temp
        }
        console.log(this.bgStyle.posX)
        console.log(this.bgStyle.posY)
        $('.exhibition').css('background-position', this.bgStyle.posX + '% ' + this.bgStyle.posY + '%')
        console.log($('.exhibition').css('background-position'))
      }
    },
    clearBackground: function () {
      this.bg = false
      this.bgStyle = {
        scale: 125,
        posX: 50,
        posY: 50
      }
      $('#bg_img').val(null)
      $('.exhibition').css('background', '')
    },
    bgPicker: function (e) {
      var files = e.target.files || e.dataTransfer.files
      var self = this
      if (!files.length) {
        return
      }
      // eslint-disable-next-line
      lrz(files[0], {
        height: 600
      })
        .then(function (rst) {
          var sourceSize = files[0].size / 1024
          var resultSize = rst.fileLen / 1024
          var scale = parseInt(100 - (resultSize / sourceSize * 100))
          alert('Reduce Size Over ' + scale + '%')
          // self.setHead = true
          // self.user = rst.base64
          // self.$root.head = rst.base64
          // $.ajax({
          //   url: '/user_update.php',
          //   type: 'POST',
          //   data: {base64: rst.base64},
          //   success: function (data) {
          //     console.log(data)
          //   }
          // })
          $('.exhibition').css('background', 'url(\'' + rst.base64 + '\') no-repeat')
          $('.exhibition').css('background-size', self.bgStyle.scale + '%')
          $('.exhibition').css('background-position', self.bgStyle.posX + '% ' + self.bgStyle.posY + '%')
          self.bg = true
          return rst
        })
    },
    renderInfo: function (i) {
      if (this.$root.adapt) {
        switch (i) {
          case 0:
            return 'font-size: 24px; font-weight: bold;'
          case 1:
          case 2:
          case 3:
            return 'font-size: 18px;'
        }
      } else {
        switch (i) {
          case 0:
            return 'font-size: 18px; font-weight: bold;'
          case 1:
          case 2:
          case 3:
            return 'font-size: 18px;'
        }
      }
    },
    deFocus: function () {
      this.inputIndex = -1
    },
    submit: function () {
      var tag = true
      var data = $('.newsInfo input')
      for (var j = 0; j < data.length; j++) {
        if (data[j].value === '') {
          tag = false
          this.newsInfo[j].error = true
        }
      }
      var formData = new FormData()
      var content = $('.newsInfo textarea').val()
      if (content === '') {
        tag = false
        this.newsInfo[4].error = true
      }
      var file = $('#bg_img').get(0).files[0]
      if (typeof (file) === 'undefined') {
        tag = false
        var mode = {
          text: '请上传新闻图片！',
          duration: 1200
        }
        this.$root.eventHub.$emit('showPrompt', mode)
      }
      if (tag) {
        formData.append('title', data[0].value)
        formData.append('bg_img', file)
        var str = 'background-size: cover; background-position: ' + $('.exhibition').css('background-position') + ';'
        formData.append('bg_params', str)
        formData.append('news_date', data[1].value)
        formData.append('news_source', data[2].value)
        formData.append('news_url', data[3].value)
        formData.append('news_content', content)
        // eslint-disable-next-line no-undef
        formData.append('csrfmiddlewaretoken', csrf_token)
        $.ajax({
          async: false,
          contentType: false,
          processData: false,
          url: '/upload_news/',
          type: 'POST',
          data: formData,
          success: function (data) {
            alert(data)
          }
        })
      }
    },
    focus: function (i) {
      this.inputIndex = i
      switch (i) {
        case 1:
          if (this.newsInfo[1].error) {
            this.newsInfo[1].error = false
          }
          if (event.target.value === '') {
            var d = new Date()
            var str = d.getFullYear() + '年' + (d.getMonth() + 1) + '月' + d.getDate() + '日'
            event.target.value = str
          }
          break
      }
    },
    write: function (i) {
      console.log(1)
      if (event.target.value !== '') {
        if (this.newsInfo[i].error) {
          this.newsInfo[i].error = false
        }
      }
    }
  },
  mounted () {
    this.uploadStatus = true
    setTimeout(function () {
      $('#app').animate({scrollTop: $('#main').height() + 2}, 300)
    }, 17)
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .background-chooser{
    text-align: center;
    margin-top: 20px;
    position: relative;
    overflow: hidden;
    background: #E6E6E6;
  }
  .exhibition{
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 0;
  }
  .picker{
    border-radius: 50%;
    background: url("../../resource/assets/upload.png");
    background-size: cover;
    position: absolute;
    z-index: 3;
  }
  .clear{
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.2);
    position: absolute;
  }
  .clear>div{
    margin: 8px;
    border-radius: 50%;
    background: url("../../resource/assets/close.png");
    background-size: cover;
  }
  #bg_img{
    width: 1px;
    height: 1px;
    visibility: hidden;
  }
  .newsInfo{
    border: none;
    outline: none;
  }
  .newsInfo:not(:last-of-type).active{
    height: 39px;
    border-bottom: 2px solid #3cb371;
  }
  .newsInfo:not(:last-of-type).error{
    height: 39px;
    border-bottom: 2px solid #dc143c!important;
  }
  .news-content{
    border: 1px solid #f0f2f5;
    border-radius: 3px;
    outline: none;
    resize: none;
  }
  .news-content.active{
    border: 2px solid #3cb371;
  }
  .news-content.error{
    border: 2px solid #dc143c!important;
  }
  #submit{
    text-align: center;
    color: white;
    margin-bottom: 50px;
    background: #3cb371;
    border-radius: 3px;
    user-select: none;
    -webkit-transition: background-color ease-out .2s;
    -moz-transition: background-color ease-out .2s;
    -o-transition: background-color ease-out .2s;
    transition: background-color ease-out .2s;
  }
  #submit:active{
    background: #2EA057;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation{
      width: calc(100% - 20px);
      margin: 0 10px;
    }
    .background-chooser{
      width: calc(100% - 20px);
      margin: 0 10px;
      top: 20px;
      height: 220px;
    }
    .picker{
      width: 80px;
      height: 80px;
      top: calc((100% - 80px) / 2);
      left: calc((100% - 80px) / 2);
    }
    .clear{
      width: 36px;
      height: 36px;
      top: 14px;
      right: 14px;
    }
    .clear>div{
      width: 20px;
      height: 20px;
    }
    .clear:hover, .clear:active{
      -webkit-transform: scale(0.9);
      -moz-transform: scale(0.9);
      -o-transform: scale(0.9);
      transform: scale(0.9);
    }
    .newsInfo:nth-of-type(2){
      margin-top: 20px;
    }
    .newsInfo{
      height: 41px;
      margin: 0 10px;
      padding: 8px 0;
    }
    .newsInfo:not(:last-of-type){
      border-bottom: 1px solid #f0f2f5;
    }
    .newsInfo input{
      width: 100%;
      height: 41px;
      line-height: 41px;
      border: none;
      outline: none;
    }
    .newsInfo:not(:last-of-type).active{
      height: 40px;
    }
    .newsInfo:not(:last-of-type).error{
      height: 40px;
    }
    .newsInfo:nth-of-type(6){
      height: 274px;
    }
    .news-content{
      width: calc(100% - 14px);
      height: 240px;
      margin: 10px 0;
      padding: 8px 6px;
      font-size: 18px;
    }
    .news-content.active{
      margin: 9px 0;
      width: calc(100% - 16px);
    }
    #submit{
      width: calc(100% - 20px);
      height: 42px;
      margin: 0 10px 50px;
      font-size: 18px;
      line-height: 42px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation{
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .background-chooser{
      width: 100%;
      height: 450px;
    }
    .picker{
      width: 90px;
      height: 90px;
      top: calc((100% - 90px) / 2);
      left: calc((100% - 90px) / 2);
    }
    .clear{
      width: 48px;
      height: 48px;
      top: 12px;
      right: 12px
    }
    .clear>div{
      width: 32px;
      height: 32px;
    }
    .clear:hover>div, .clear:active>div{
      width: 28px;
      height: 28px;
      margin: 10px;
    }
    .newsInfo{
      width: 100%;
      padding: 9px 0;
    }
    .newsInfo:not(:last-of-type){
      border-bottom: 1px solid #f0f2f5;
    }
    .newsInfo input{
      width: calc(100% - 20px);
      height: 39px;
      margin: 0 10px;
      border: none;
      outline: none;
      color: black;
    }
    .newsInfo:first-of-type{
      margin-top: 18px;
    }
    .newsInfo:nth-of-type(3){
      margin-right: 20px;
    }
    .newsInfo:nth-of-type(3), .newsInfo:nth-of-type(4){
      width: calc((100% - 20px) / 2);
    }
    .newsInfo:nth-of-type(3), .newsInfo:nth-of-type(4), .newsInfo:nth-of-type(5){
      float: left;
    }
    .newsInfo:nth-of-type(6){
      clear: both;
    }
    .newsInfo:nth-of-type(6){
      height: 530px;
    }
    .news-content{
      width: calc(100% - 20px);
      height: 500px;
      margin: 16px 0;
      padding: 12px 10px;
      font-size: 18px;
    }
    .news-content.active{
      margin: 15px 0;
      width: calc(100% - 22px);
    }
    #submit{
      width: 200px;
      height: 42px;
      margin: 30px calc((100% - 200px) / 2);
      font-size: 18px;
      line-height: 42px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-width: 720px) {
    .newsInfo:nth-of-type(3), .newsInfo:nth-of-type(4){
      width: 150px;
      margin-right: 20px;
    }
    .newsInfo:nth-of-type(5){
      width: calc(100% - 340px);
    }
  }
</style>



